button {
	border: none;
	padding: 10px 25px;
	cursor: pointer;
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
}

button:hover {
	background: #222;
	color: #fff;
}

button:active,
button:focus {
	outline: none;
}


